  width: 100%;
}

.textfield::placeholder {
  color: #b8b8b8;
}

.form__select {
  display: none;
}

.form__field {
  display: inline-block;
}

.form__field--lg {
  width: 340px;
}

@media only screen and (max-width: 1023px) {
  .form__field--lg {
    width: 300px;
  }
}

@media only screen and (max-width: 767px) {
  .form__field--lg {
    width: 100%;
  }
}

.form__field--lg .custom-select {
  border-radius: 30px;
}

.form__field--icon .custom-select__field {
  margin-right: 30px;
  margin-left: 29px;
  padding-left: 24px;
  position: relative;
}

.form__field--icon .custom-select__field .icon {
  transition: all 0.3s;
  height: 12px;
  width: 18px;
  stroke: #b8b8b8;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.form__field--icon .custom-select__field .icon-label {
  height: 14px;
  width: 17px;
  fill: #b8b8b8;
  left: 0;
}

.custom-select {
  display: inline-block;
  position: relative;
  cursor: pointer;
  color: #b8b8b8;
  background-color: #ffffff;
  font-family: Panton-Regular, Arial, sans-serif;
  width: 100%;
}

.custom-select__field {
  padding: 22px 10px 19px;
}

.custom-select__dropdown {
  padding: 20px;
  background-color: #ffffff;
  line-height: 30px;
  border-radius: 30px;
  transition: all 0.2s;
  position: absolute;
  bottom: 0;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  width: 100%;
}

.custom-select.is-open .custom-select__dropdown {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.custom-select.is-open .custom-select__field .icon {
  transform: translateY(-50%) rotate(180deg);
}

.custom-select.is-open .custom-select__field .icon-label {
  transform: translateY(-50%) rotate(0);
}

.custom-select__list li:hover {
  color: #14c1e5;
}
