.filters {
	@include clr;
	&__field {
		width: 224px;
		float: left;
		padding-top: 6px;
		padding-left: 4px; }
	&__title {
		font-size: 13px;
		@include PReg;
		margin-bottom: 8px;
		padding-left: 6px; }
	&__list {
		width: calc(100% - 224px);
		padding-left: 42px;
		float: left; } }
.tagi {
	ul {
		font-size: 0;
		margin: 0 -5px;
		li {
			display: inline-block;
			vertical-align: middle;
			padding: 0 5px;
			margin-bottom: 10px;
			a {
				padding: 7px 13px;
				color: $cyan;
				font-size: 13px;
				border-radius: 20px;
				border: 1px solid #b8ecf7;
				display: inline-block;
				@include OSReg;
				background-color: #edf9fb;
				min-height: 30px;
				line-height: 1;
				text-align: center;
				transition: .25s ease;
				&:hover {
					color: #fff;
					background-color: $cyan; } }
			&.is-active {
				a {
					background-color: $cyan;
					color: #fff; } } } } }
